import { useVideos } from "@/hooks/useVideos";
import Video from "./Video";

export default function AllVideos({ isSidebarOpen }) {
  const { videos, loading, error } = useVideos();

  return (
    <div className="border-2 border-amber-600 px-3 w-4/5 flex gap-3">
      {videos.map((video) => (
        <Video
          key={video._id}
          video={video}
          loading={loading}
          error={error}
          isSidebarOpen={isSidebarOpen}
        />
      ))}
    </div>
  );
}
